// Modular Scale ==============================================================
@import 'mathsass/dist/_math';
@import 'modularscale-sass/stylesheets/modularscale';

@import 'build/sass/functions';
@import 'build/sass/variables';
@import 'build/sass/mixins';
// Only put styles that apply to the Style Guide page

#style-guide {
    > .section {
        > .section-inner {
            padding-top: 0 !important;
            padding-bottom: 0 !important;
            > .container {
                padding-top: ms(4);
                padding-bottom: ms(4);
            }
        }
        & + .section {
            > .section-inner {
                > .container {
                    border-top: 1px solid $border-color;
                }
            }
        }
    }
    .content-item {
        margin: ms(1) 0;
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
}
